import { useState, useEffect } from "react";
import axios from "axios";
import { useForm } from "react-hook-form";
import { toast } from "react-toastify";

const EditCustommer = (props) => {
  const [IsLoading, SetIsLoading] = useState(true);
  const {
    register,
    setValue,
    formState: { errors },
    handleSubmit,
    reset,
  } = useForm({
    mode: "onChange",
  });

  const [ListProvince, SetListProvince] = useState([]);
  const [ListDistrict, SetListDistrict] = useState([]);
  const [ListWard, SetListWard] = useState([]);
  const [DataForm, SetDataForm] = useState({});

  const onSubmit = async (data) => {
    SetIsLoading(true);

    await axios
      .put(
        `http://localhost:8088/api/Custommer/EdtiCustommer?CustommerId=${data.MaKH}`,
        {
          maKh: data.MaKH.toUpperCase(),
          tenKh: data.TenKH,
          maSoThue: data.MST,
          sdt: data.SDT,
          email: data.Email,
          address: {
            tenDiaDiem: "",
            maQuocGia: 1,
            maTinh: parseInt(data.MaTinh),
            maHuyen: parseInt(data.MaHuyen),
            maPhuong: parseInt(data.MaPhuong),
            soNha: data.SoNha,
            diaChiDayDu: "",
            maGps: data.GPS,
            maLoaiDiaDiem: "1",
          },
        },
        {
          accept: "*/*",
          "Content-Type": "application/json",
        }
      )
      .then(
        (response) => {
          props.getListUser(1);
          toast.success(response.data);
        },
        (error) => {
          toast.success(error.response.data);
        }
      );

    SetIsLoading(false);
  };

  useEffect(() => {
    reset();
    if (
      props &&
      props.selectIdClick &&
      props.Address &&
      Object.keys(props.Address).length > 0 &&
      Object.keys(props.selectIdClick).length > 0
    ) {
      SetDataForm(props.selectIdClick);
      setValue("MaKH", props.selectIdClick.maKh);
      setValue("MST", props.selectIdClick.maSoThue);
      setValue("SDT", props.selectIdClick.sdt);
      setValue("TenKH", props.selectIdClick.tenKh);
      setValue("Email", props.selectIdClick.email);
      setValue("GPS", props.Address.maGps);
      setValue("SoNha", props.Address.sonha);

      async function getAddress() {
        await LoadDistrict(props.Address.matinh);
        await LoadWard(props.Address.mahuyen);
        setValue("MaTinh", props.Address.matinh);
        setValue("MaHuyen", props.Address.mahuyen);
        setValue("MaPhuong", props.Address.maphuong);
      }
      getAddress();
    }
  }, [props.selectIdClick, props.Address]);

  useEffect(() => {
    SetIsLoading(true);

    SetListProvince([]);
    SetListDistrict([]);
    SetListWard([]);

    async function getProvince() {
      const listProvince = await axios.get(
        "http://localhost:8088/api/address/ListProvinces"
      );

      if (listProvince && listProvince.data && listProvince.data.length > 0) {
        SetListProvince(listProvince.data);
      }
    }

    getProvince();

    if (props && props.selectIdClick) {
      SetDataForm(props.selectIdClick);
    }
    SetIsLoading(false);
  }, []);

  const LoadDistrict = async (val) => {
    const listDistrict = await axios.get(
      `http://localhost:8088/api/address/ListDistricts?ProvinceId=${val}`
    );
    if (listDistrict && listDistrict.data && listDistrict.data.length > 0) {
      SetListDistrict(listDistrict.data);
    } else {
      SetListDistrict([]);
    }
  };

  const LoadWard = async (val) => {
    const listWard = await axios.get(
      `http://localhost:8088/api/address/ListWards?DistrictId=${val}`
    );

    if (listWard && listWard.data && listWard.data.length > 0) {
      SetListWard(listWard.data);
    } else {
      SetListWard([]);
    }
  };

  const HandleChangeProvince = (val) => {
    try {
      SetListDistrict([]);
      SetListWard([]);
      setValue("MaHuyen", "");
      setValue("MaPhuong", "");
      SetIsLoading(true);
      if (val === undefined || val === "") {
        SetListDistrict([]);
        SetListWard([]);
        return;
      }
      LoadDistrict(val);
      SetIsLoading(false);
    } catch (error) {}
  };

  const HandleOnchangeDistrict = (val) => {
    try {
      SetIsLoading(true);

      if (val === undefined || val === "") {
        SetListWard([]);
        return;
      }
      LoadWard(val);
      SetIsLoading(false);
    } catch (error) {}
  };
  return (
    <>
      <div className="card card-primary">
        <div className="card-header">
          <h3 className="card-title">Form Cp nht Khch Hng</h3>
        </div>
        <div>{IsLoading === true && <div>Loading...</div>}</div>

        {IsLoading === false && (
          <form onSubmit={handleSubmit(onSubmit)}>
            <div className="card-body">
              <div className="form-group">
                <label htmlFor="MaKH">M khch hng</label>
                <input
                  autoComplete="false"
                  type="text"
                  className="form-control"
                  id="MaKH"
                  placeholder="Nhp m khch hng"
                  {...register("MaKH", {
                    required: "Khng c  trng",
                    maxLength: {
                      value: 8,
                      message: "Khng c vt qu 8 k t",
                    },
                    minLength: {
                      value: 8,
                      message: "Khng c t hn 8 k t",
                    },
                    pattern: {
                      value:
                        /^(?![_.])(?![_.])(?!.*[_.]{2})[a-zA-Z0-9]+(?<![_.])$/,
                      message: "Khng c cha k t c bit",
                    },
                  })}
                />
                {errors.MaKH && (
                  <span className="text-danger">{errors.MaKH.message}</span>
                )}
              </div>
              <div className="form-group">
                <label htmlFor="TenKH">Tn khch hng</label>
                <input
                  type="text"
                  className="form-control"
                  id="TenKH"
                  placeholder="Nhp tn khch hng"
                  {...register("TenKH", {
                    required: "Khng c  trng",
                    maxLength: {
                      value: 50,
                      message: "Khng c vt qu 50 k t",
                    },
                    minLength: {
                      value: 1,
                      message: "Khng c t hn 1 k t",
                    },
                    pattern: {
                      value:
                        /^(?![_.])(?![_.])(?!.*[_.]{2})[a-zA-Z0-9 aAbBcCdDeE fFgGhHiIjJkKlLmMnNoOpPqQrRsStTu UvVwWxXyYzZ]+(?<![_.])$/,
                      message: "Tn khch hng khng c cha k t c bit",
                    },
                  })}
                />
                {errors.TenKH && (
                  <span className="text-danger">{errors.TenKH.message}</span>
                )}
              </div>
              <div className="form-group">
                <label htmlFor="Email">a ch Email</label>
                <input
                  type="text "
                  className="form-control"
                  id="Email"
                  placeholder="Nhp a ch Email"
                  {...register("Email", {
                    required: "Khng c  trng",
                    maxLength: {
                      value: 100,
                      message: "Khng c vt qu 100 k t",
                    },
                    minLength: {
                      value: 3,
                      message: "Khng c t hn 3 k t",
                    },
                    pattern: {
                      value: /^([\w\.\-]+)@([\w\-]+)((\.(\w){2,3})+)$/,
                      message: "Khng phi Email",
                    },
                  })}
                />
                {errors.Email && (
                  <span className="text-danger">{errors.Email.message}</span>
                )}
              </div>
              <div className="form-group">
                <label htmlFor="MST">M s thu</label>
                <input
                  type="text "
                  className="form-control"
                  id="MST"
                  placeholder="Nhp m s thu"
                  {...register("MST", {
                    required: "Khng c  trng",
                    maxLength: {
                      value: 50,
                      message: "Khng c vt qu 50 k t",
                    },
                    minLength: {
                      value: 1,
                      message: "Khng c t hn 1 k t",
                    },
                    pattern: {
                      value: /^(?![_.])(?![_.])(?!.*[_.]{2})[0-9]+(?<![_.])$/,
                      message: "M s thu ch c cha k t l s",
                    },
                  })}
                />
                {errors.MST && (
                  <span className="text-danger">{errors.MST.message}</span>
                )}
              </div>
              <div className="form-group">
                <label htmlFor="SDT">S in thoi</label>
                <input
                  type="text"
                  className="form-control"
                  id="SDT"
                  placeholder="Nhp s in thoi"
                  {...register("SDT", {
                    required: "Khng c  trng",
                    maxLength: {
                      value: 50,
                      message: "Khng c vt qu 20 k t",
                    },
                    minLength: {
                      value: 10,
                      message: "Khng c t hn 10 k t",
                    },
                    pattern: {
                      value: /^(?![_.])(?![_.])(?!.*[_.]{2})[0-9]+(?<![_.])$/,
                      message: "S in thoi ch c cha k t l s",
                    },
                  })}
                />
                {errors.SDT && (
                  <span className="text-danger">{errors.SDT.message}</span>
                )}
              </div>
              <div className="form-group">
                <label htmlFor="GPS">M GPS</label>
                <input
                  type="text"
                  className="form-control"
                  id="GPS"
                  placeholder="Nhp m GPS"
                  {...register("GPS", {
                    required: "Khng c  trng",
                    maxLength: {
                      value: 50,
                      message: "Khng c vt qu 50 k t",
                    },
                    minLength: {
                      value: 1,
                      message: "Khng c t hn 1 k t",
                    },
                  })}
                />
                {errors.GPS && (
                  <span className="text-danger">{errors.GPS.message}</span>
                )}
              </div>
              <div className="row">
                <div className="col-sm">
                  <div className="form-group">
                    <label htmlFor="Sonha">S nh</label>
                    <input
                      type="text"
                      className="form-control"
                      id="Sonha"
                      placeholder="Nhp s nh"
                      {...register("SoNha", {
                        required: "Khng c  trng",
                        maxLength: {
                          value: 100,
                          message: "Khng c vt qu 100 k t",
                        },
                        minLength: {
                          value: 1,
                          message: "Khng c t hn 1 k t",
                        },
                      })}
                    />
                    {errors.SoNha && (
                      <span className="text-danger">
                        {errors.SoNha.message}
                      </span>
                    )}
                  </div>
                </div>
                <div className="col-sm">
                  <div className="form-group">
                    <label htmlFor="Tinh">Tnh</label>
                    <select
                      className="form-control"
                      id="inputGroupSelect01"
                      {...register("MaTinh", {
                        required: "Khng c  trng",
                        onChange: (e) => HandleChangeProvince(e.target.value),
                      })}
                    >
                      <option value="">Chn tnh...</option>
                      {ListProvince &&
                        ListProvince.length > 0 &&
                        ListProvince.map((val) => {
                          return (
                            <option key={val.maTinh} value={val.maTinh}>
                              {val.maTinh} - {val.tenTinh}
                            </option>
                          );
                        })}
                    </select>
                    {errors.maTinh && (
                      <span className="text-danger">
                        {errors.maTinh.message}
                      </span>
                    )}
                  </div>
                </div>
                <div className="col-sm">
                  <div className="form-group">
                    <label htmlFor="SDT">Huyn</label>
                    <select
                      className="form-control"
                      id="inputGroupSelect01"
                      {...register("MaHuyen", {
                        required: "Khng c  trng",
                        onChange: (e) => HandleOnchangeDistrict(e.target.value),
                      })}
                    >
                      <option value="">Chn Huyn...</option>
                      {ListDistrict &&
                        ListDistrict.length > 0 &&
                        ListDistrict.map((val) => {
                          return (
                            <option key={val.maHuyen} value={val.maHuyen}>
                              {val.maHuyen} - {val.tenHuyen}
                            </option>
                          );
                        })}
                    </select>
                    {errors.MaHuyen && (
                      <span className="text-danger">
                        {errors.MaHuyen.message}
                      </span>
                    )}
                  </div>
                </div>
                <div className="col-sm">
                  <div className="form-group">
                    <label htmlFor="SDT">Phng</label>
                    <select
                      className="form-control"
                      id="inputGroupSelect01"
                      {...register("MaPhuong", {
                        required: "Khng c  trng",
                      })}
                    >
                      <option value="">Chn phng</option>
                      {ListWard &&
                        ListWard.length > 0 &&
                        ListWard.map((val) => {
                          return (
                            <option key={val.maPhuong} value={val.maPhuong}>
                              {val.maPhuong} - {val.tenPhuong}
                            </option>
                          );
                        })}
                    </select>
                    {errors.MaPhuong && (
                      <span className="text-danger">
                        {errors.MaPhuong.message}
                      </span>
                    )}
                  </div>
                </div>
              </div>
            </div>
            <div className="card-footer">
              <div>
                <button
                  type="submit"
                  className="btn btn-primary"
                  style={{ float: "right" }}
                >
                  Cp nht
                </button>
              </div>
            </div>
          </form>
        )}
      </div>
    </>
  );
};

export default EditCustommer;
